<script setup lang="ts">
import { ref, computed } from 'vue'
import type { PropType } from 'vue'
import type { PageConfig, Word } from '@/types'
import { ExternalLink, Search, Check } from 'lucide-vue-next'

const props = defineProps({
  config: {
    type: Object as PropType<PageConfig>,
    required: true
  }
})

const emit = defineEmits<{
  (e: 'jump-to', pageNumber: number): void
}>()

const searchQuery = ref('')

// Reactive state for learned status (local for now, could be persisted)
const learnedState = ref<Record<string, boolean>>({})

const toggleLearned = (wordText: string) => {
  learnedState.value[wordText] = !learnedState.value[wordText]
}

const filteredGroups = computed(() => {
  if (!props.config.wordGroups) return []
  
  if (!searchQuery.value) return props.config.wordGroups

  const query = searchQuery.value.toLowerCase()
  return props.config.wordGroups.map(group => ({
    ...group,
    words: group.words.filter(w => w.text.toLowerCase().includes(query))
  })).filter(group => group.words.length > 0)
})

const getRoleColor = (role: string, learned: boolean) => {
  if (learned) return 'bg-slate-100 text-slate-400 border-slate-200 decoration-slate-400'
  
  switch (role) {
    case 'dev': return 'bg-blue-50 text-blue-600 border-blue-100 hover:bg-blue-100 hover:border-blue-200'
    case 'biz': return 'bg-green-50 text-green-600 border-green-100 hover:bg-green-100 hover:border-green-200'
    case 'all': return 'bg-purple-50 text-purple-600 border-purple-100 hover:bg-purple-100 hover:border-purple-200'
    default: return 'bg-slate-50 text-slate-600 border-slate-100'
  }
}
</script>

<template>
  <div class="h-full w-full flex flex-col items-center justify-center bg-slate-50 p-8">
    <div class="max-w-6xl w-full h-full flex flex-col">
      <div class="flex items-center justify-between mb-8 shrink-0">
        <div>
          <h2 class="text-3xl font-bold text-slate-900">{{ config.title }}</h2>
          <p class="text-slate-500 text-sm mt-1">{{ config.subTitle }}</p>
        </div>
        
        <!-- Search -->
        <div class="relative w-64">
          <Search class="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
          <input 
            v-model="searchQuery"
            type="text"
            placeholder="Find a term..."
            class="w-full pl-10 pr-4 py-2 rounded-full border border-slate-200 bg-white text-sm focus:outline-none focus:ring-2 focus:ring-blue-500/20 focus:border-blue-500 transition-all"
            @keydown.stop
          />
        </div>
      </div>

      <!-- Categories Grid -->
      <div 
        class="flex-1 min-h-0 overflow-y-auto pr-2 pb-8 scroll-smooth"
        @wheel.stop
      >
        <div class="masonry-grid">
          <div 
            v-for="group in filteredGroups" 
            :key="group.name"
            class="break-inside-avoid mb-6 bg-white rounded-xl p-5 shadow-sm border border-slate-100"
          >
            <h3 class="font-bold text-slate-800 text-sm uppercase tracking-wider mb-4 pb-2 border-b border-slate-50">
              {{ group.name }}
            </h3>
            
            <div class="flex flex-wrap gap-2">
              <div 
                v-for="word in group.words" 
                :key="word.text"
                class="group flex items-stretch rounded-lg border text-sm transition-all duration-200 overflow-hidden"
                :class="[
                  getRoleColor(word.role, !!learnedState[word.text]),
                  learnedState[word.text] ? 'opacity-75' : 'hover:shadow-sm hover:-translate-y-0.5'
                ]"
              >
                <!-- Main Body: Mark as Learned -->
                <button 
                  class="px-3 py-1.5 flex items-center gap-1.5 text-left outline-none"
                  @click="toggleLearned(word.text)"
                >
                  <Check v-if="learnedState[word.text]" class="w-3.5 h-3.5" />
                  <span class="font-medium" :class="{ 'line-through': learnedState[word.text] }">{{ word.text }}</span>
                </button>

                <!-- Right Action: Go to Slide -->
                <button 
                  v-if="word.link?.pageNumber"
                  class="px-2 py-1.5 border-l border-black/5 hover:bg-black/5 flex items-center justify-center transition-colors outline-none"
                  title="Go to slide"
                  @click.stop="emit('jump-to', word.link.pageNumber)"
                >
                  <ExternalLink class="w-3.5 h-3.5 opacity-60 group-hover:opacity-100 transition-opacity" />
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Legend Footer -->
      <div class="mt-auto pt-4 border-t border-slate-200 flex justify-center gap-6 text-xs font-medium text-slate-500 shrink-0">
        <div class="flex items-center gap-2">
          <div class="w-2 h-2 rounded-full bg-blue-500"></div>
          <span>Technical</span>
        </div>
        <div class="flex items-center gap-2">
          <div class="w-2 h-2 rounded-full bg-green-500"></div>
          <span>Business</span>
        </div>
        <div class="flex items-center gap-2">
          <div class="w-2 h-2 rounded-full bg-purple-500"></div>
          <span>General</span>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.masonry-grid {
  column-count: 1;
  column-gap: 1.5rem;
}

@media (min-width: 768px) {
  .masonry-grid {
    column-count: 2;
  }
}

@media (min-width: 1024px) {
  .masonry-grid {
    column-count: 3;
  }
}

@media (min-width: 1280px) {
  .masonry-grid {
    column-count: 4;
  }
}
</style>